/** @format */

"use client";

import { TaskListProps } from "@/types";
import React, {
  createContext,
  useContext,
  useEffect,
  useState,
  useCallback,
} from "react";

export interface TaskContextType {
  todos: TaskListProps[];
  setTodos: React.Dispatch<React.SetStateAction<TaskListProps[]>>; // Keep setTodos if TasksPage needs direct control
  addOrUpdateTask: (task: TaskListProps) => void;
  syncTodos: (apiTodos: TaskListProps[]) => void; // New function to merge API data
}

const TaskContext = createContext<TaskContextType | undefined>(undefined);

export const TaskProvider = ({ children }: { children: React.ReactNode }) => {
  const [todos, setTodos] = useState<TaskListProps[]>([]);

  // Load from localStorage on initial mount
  useEffect(() => {
    try {
      const stored = localStorage.getItem("tasks");
      if (stored) {
        const parsedTodos: TaskListProps[] = JSON.parse(stored);
        setTodos(parsedTodos);
        console.log("TaskContext: Loaded tasks from localStorage.");
      }
    } catch (error) {
      console.error(
        "TaskContext: Failed to parse tasks from localStorage",
        error
      );
      localStorage.removeItem("tasks"); // Clear potentially corrupted data
    }
  }, []); // Empty dependency array means this runs once on mount

  // Function to add/update a single task (used by TaskForm and TaskList for immediate updates)
  const addOrUpdateTask = useCallback((task: TaskListProps) => {
    setTodos((prev) => {
      const exists = prev.find((t) => t.id === task.id);
      let updatedTodos;
      if (exists) {
        // If task exists, update it
        updatedTodos = prev.map((t) => (t.id === task.id ? task : t));
      } else {
        // If new task, add it to the BEGINNING of the list for better visibility
        updatedTodos = [task, ...prev];
      }
      localStorage.setItem("tasks", JSON.stringify(updatedTodos));
      console.log("TaskContext: Task added/updated, localStorage updated.");
      return updatedTodos;
    });
  }, []);

  // New function to synchronize with API data
  const syncTodos = useCallback((apiTodos: TaskListProps[]) => {
    setTodos((prevLocalTodos) => {
      const updatedTodosMap = new Map<number, TaskListProps>();

      // 1. Populate map with existing local todos
      prevLocalTodos.forEach((todo) => updatedTodosMap.set(todo.id, todo));

      // 2. Overwrite/add with API todos (API is generally authoritative for core data)
      apiTodos.forEach((apiTodo) => updatedTodosMap.set(apiTodo.id, apiTodo));

      // 3. Convert map back to array. You might want to sort these (e.g., by ID, or by creation date if available)
      // For now, let's keep the order consistent by sorting by ID.
      const mergedTodos = Array.from(updatedTodosMap.values()).sort(
        (a, b) => a.id - b.id
      );

      localStorage.setItem("tasks", JSON.stringify(mergedTodos));
      console.log("TaskContext: Synced with API data, localStorage updated.");
      return mergedTodos;
    });
  }, []);

  // The context value will expose all necessary states and functions
  const contextValue = {
    todos,
    setTodos, // Expose setTodos if TasksPage still needs to set the entire array
    addOrUpdateTask,
    syncTodos, // Expose the new sync function
  };

  return (
    <TaskContext.Provider value={contextValue}>{children}</TaskContext.Provider>
  );
};

export const useTaskContext = () => {
  const context = useContext(TaskContext);
  if (!context) {
    throw new Error("useTaskContext must be used within a TaskProvider");
  }
  return context;
};
